/**
 * toFixed è§£å³jsç²¾åº¦é®é¢ï¼ä½¿ç¨æ¹å¼ï¼toFixed(value)
 * @param <Number | String> value
 * @param <Number> precision ç²¾åº¦ï¼é»è®¤2ä½å°æ°ï¼éè¦åæ´åä¼ 0
 * è¯¥æ¹æ³ä¼å¤çå¥½ä»¥ä¸è¿äºé®é¢ï¼
 * 1.12*100=112.00000000000001
 * 1.13*100=112.9999999999999
 * '0.015'.toFixed(2)ç»æä¸º0.01
 * 1121.1/100 = 11.210999999999999
 */
export const toFixed = (value, precision = 2) => {
  const num = Number(value);
  if (Number.isNaN(num)) {
    return 0;
  }
  if (num < Math.pow(-2, 31) || num > Math.pow(2, 31) - 1) {
    return 0;
  }
  // console.log(num, precision)
  if (precision < 0 || typeof precision !== 'number') {
    return value;
  } else if (precision > 0) {
    return Math.round(num * Math.pow(10, precision)) / Math.pow(10, precision);
  }
  return Math.round(num);
};

/**
 * åä½ä¸ºåæ°å¼è½¬ä¸ºå
 * @param <Number> value
 */
export const toCentNumber = value => {
  const num = Number(value);
  if (Number.isNaN(num)) {
    return 0;
  }
  return toFixed(num * 100, 0);
};

/**
 * åæ°å¼æ°å¼è½¬ä¸ºå
 * @param <Number> centval åä¸ºåä½
 * @param <Number> precision ç²¾åº¦ï¼é»è®¤2ä½å°æ°
 */
export const toYuanNumber = (centval, precision = 2) => {
  const num = Number(centval);
  if (Number.isNaN(num)) {
    return 0;
  }
  return toFixed(num / 100, precision);
};
